import * as React from 'react';

import './App.css';

import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';

import { MapApp } from './MapApp';

interface IUser {
  name: string;
}

class App extends React.Component<{}, { user: IUser | null, form: { name: string }}> {

  public state = { user: null, form: { name: '' } };

  public render() {
    return (
      <div className="App">
        <AppBar position="static" color="default">
          <Toolbar>
            <Typography variant="title" color="inherit">
              Trip Tracker
            </Typography>
          </Toolbar>
        </AppBar>
        {this.state.user != null && <MapApp />}
        {this.state.user == null && <section>
          <TextField
            id="with-placeholder"
            label="Your Name"
            placeholder="What do people call you?"
            margin="normal"
            value={this.state.form.name}
            onChange={this.onChange}
          />
          <Button variant="contained" color="primary" onClick={this.onClick}>Submit</Button>
        </section>}
      </div>
    );
  }

  private onChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    this.setState({ ...this.state, form: { name: event.currentTarget.value }});
  }

  private onClick = () => {
    this.setState({ user: { name: this.state.form.name } });
  }
}

export default App;



